<template>
  <div class="building pt60">
    <narBar></narBar>
    <topBar :titleInfo="titleInfo"></topBar>
    <div class="pt30 pb30 bg-fff">
        <currentTab :productList="productList"></currentTab>
        <div class="public_content">
            <barTitle :title="title"></barTitle>
            <div class="fs20 building_item"></div>
        </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      titleInfo: {
        themem: "",
        title: "Scope of Technical Service",
        // companyInfo:
        //   "2017 2017"
      },
      title: "",
      productList: [
        {
          id: 0,
          title: "",
          path: "/building"
        },
        {
          id: 1,
          title: "",
          path: "/advice"
        },
        {
          id: 2,
          title: "",
          path: "/webDevelop"
        },
        {
          id: 3,
          title: "APP",
          path: "/appDevelop"
        },
        {
          id: 4,
          title: "",
          path: "/weChatDevelop"
        },
        {
          id: 5,
          title: "",
          path: "/appletDevelop"
        }
      ],
    };
  }
};
</script>
<style lang="less" scoped>
@import "../styles/global";
.building /deep/ .top_bar {
    background-image: url("../assets/src/images/banner-info3.png");
}
.building {
  .building_item {
    margin-top: 38px;
    margin-bottom: 618px;
  }
}
</style>


