<template>
  <div class="companyInfo pt60">
    <narBar></narBar>
    <topBar :titleInfo="titleInfo"></topBar>
    <div class="pt30 pb30 bg-fff">
      <currentTab :productList="productList"></currentTab>
      <div class="public_content">
        <barTitle :title="title"></barTitle>
        <div>
          <div class="fs28 mt40 mb20"></div>
          <div
            class="fs18 mb40"
          >201709012017+</div>
          <ul class="dfrb mb40">
            <li v-for="(item,a) in companyInfoList" :key="a" class="info_item mb40">
              <div class="info_img">
                <img :src="item.img" alt />
              </div>
              <div class="fs28 mt40 mb20">{{item.info}}</div>
              <ul>
                <li v-for="(a,b) in item.infoList" :key="b">
                  <div class="fs14">{{a.info}}</div>
                </li>
              </ul>
            </li>
            <li class="info_item_li">
                <div class="info_img">
                    <img :src="img2" alt="">
                </div>
                <div class="fs28 mt40 mb20"></div>
                <div class="fs14"></div>
            </li>
            <li class="info_item_li">
                <div class="info_img">
                    <img :src="img1" alt="">
                </div>
                <div class="fs28 mt40 mb20"></div>
                <div class="mb20 fs14">
                    <div></div>
                    <div>Give as much as anyone</div>
                </div>
                <div class="mb20 fs14">
                    <div></div>
                    <div>Be modest, not proud</div>
                </div>
                <div class="mb20 fs14">
                    <div></div>
                    <div>Good deeds and altruism</div>
                </div>
                <div class="mb20 fs14">
                    <div></div>
                    <div>Don't have emotional worries</div>
                </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      titleInfo: {
        themem: "",
        title: "About Us",
        companyInfo:
          "201709012017 "
      },
      title: "",
      productList: [
        {
          id: 0,
          title: "",
          path: "/companyInfo"
        },
        {
          id: 1,
          title: "",
          path: "/history"
        },
        {
          id: 2,
          title: "",
          path: "/culture"
        }
      ],
      companyInfoList: [
        {
          img: require("../assets/src/images/img1.png"),
          info: "",
          infoList: [
            {
              info: ""
            },
            {
              info:
                ""
            },
            {
              info:
                ""
            }
          ]
        },
        {
          img: require("../assets/src/images/img2.png"),
          info: "",
          infoList: [
            {
              info: ""
            },
            {
              info: ""
            }
          ]
        },
      ],
      img1: require("../assets/src/images/img4.png"),
      img2: require("../assets/src/images/img3.png")
    };
  }
};
</script>
<style lang="less" scoped>
@import "../styles/global";
.companyInfo /deep/ .top_bar {
  background-image: url("../assets/src/images/banner-info2.png");
}
.companyInfo {
  .info_item {
    width: 580px;
    height: 540px;
    .info_img {
      width: 580px;
      height: 360px;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  .info_item_li {
      width: 580px;
  }
}
</style>


