<template>
  <div class="history pt60">
    <narBar></narBar>
    <topBar :titleInfo="titleInfo"></topBar>
    <div class="pt30 pb30 bg-fff">
      <currentTab :productList="productList"></currentTab>
      <div class="public_content">
        <barTitle :title="title"></barTitle>
        <div style="height: 2000px;">
          <ul class="contract-operate_recode_date">
            <li v-for="(item,i) in historyList" :key="i" class="dfr">
              <div class="operate-date" :class="{'showColor': item.id == '6'}">{{item.time}}</div>
              <i></i>
              <div class="operate-des" :class="{'showColor': item.id == '6'}">{{item.product}}</div>
            </li>
            <!-- <li>
              <span>2019-01-05 13:22:32</span>
              <i></i>
              <span>1 </span>
            </li>
            <li>
              <span>2019-01-08 16:02:02</span>
              <i></i>
              <span>1 </span>
            </li>
            <li>
              <span>2019-01-10 10:11:21</span>
              <i></i>
              <span>1</span>
            </li> -->
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      titleInfo: {
        themem: "",
        title: "About Us",
        companyInfo:
          "201709012017 "
      },
      title: "",
      productList: [
        {
          id: 0,
          title: "",
          path: "/companyInfo"
        },
        {
          id: 1,
          title: "",
          path: "/history"
        },
        {
          id: 2,
          title: "",
          path: "/culture"
        }
      ],
      historyList: [
          {   
              id: "0",
              time: "20191020",
              product: "20001000 "
          },
          {
              id: "1",
              time: "2019425",
              product: ""
          },
          {
              id: "2",
              time: "2019328",
              product: ""
          },
          {
              id: "3",
              time: "201812",
              product: ""
          },
          {
              id: "4",
              time: "201810",
              product: ""
          },
          {
              id: "5",
              time: "20171016",
              product: "app"
          },
          {
              id: "6",
              time: "201791",
              product: ""
          },
          {
              id: "7",
              time: "2017414",
              product: ""
          },
          {
              id: "8",
              time: "20168",
              product: ""
          },
          {
              id: "9",
              time: "20159",
              product: ""
          },
          {
              id: "10",
              time: "20149",
              product: ""
          },
      ]
    };
  }
};
</script>
<style lang="less" scoped>
@import "../styles/global";
.history /deep/ .top_bar {
  background-image: url("../assets/src/images/banner-info2.png");
}
.contract-operate_recode_date {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  li {
    width: 100%;
    :nth-child(1) {
      width: 119px;
      height: 30px;
      line-height: 30px;
      font-size: 20px;
      display: inline-block;
      width: 200px;
      text-align: right;
    }
    :nth-child(2) {
        &:after {
        content: "";
        display: block;
        clear: both;
        width: 1px;
        height: 200px;
        background-color: @mainColor;
        margin: 8px auto;
      }
      display: inline-block;
      background-color: @mainColor !important;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      margin: 0 20px;
    }
    :nth-child(3) {
      font-size: 12px;
      display: inline-block;
      height: 16px;
      font-size: 28px;
      width: 885px;
      height: 80px;
    }
  }
  li:last-child {
    :nth-child(1) {
      font-size: 12px;
      display: inline-block;
      width: 119px;
      height: 30px;
      line-height: 30px;
      font-size: 20px;
      display: inline-block;
      width: 200px;
      text-align: right;
    }
    :nth-child(2) {
      display: inline-block;
      background-color: @mainColor !important;
      width: 18px;
      height: 18px;
      border-radius: 50%;
      &:after {
        content: "";
        display: none;
      }
    }

    :nth-child(3) {
      font-size: 12px;
      letter-spacing: 0;
      display: inline-block;
      height: 16px;
      font-size: 28px;
      width: 885px;
      height: 80px;
    }
  }
  .showColor {
      color: @mainColor;
  }
}
</style>


