<template>
  <div class="publicProduct">
    <div
      class="fs20"
    >201812201912000+</div>
    <div class="product_function text-c mt40 mb50"></div>
    <div class="show_product">
      <ul class="dfrcb">
        <li class="drc" v-for="(item,i)inimagesUrl" :key="i">
          <div class="show_product_img bg-fff">
            <img :src="item.img" alt />
          </div>
          <div class="punch">
            <div class="fs20 f600">{{item.name}}</div>
            <div class="fs14 punch_info mt20">{{item.info}}</div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      imagesUrl: [
        {
          img: require("../assets/src/images/punch.png"),
          name: "",
          info: ""
        },
        {
          img: require("../assets/src/images/take.png"),
          name: "",
          info: ""
        },
        {
          img: require("../assets/src/images/group.png"),
          name: "",
          info: ""
        },
        {
          img: require("../assets/src/images/punch_circle.png"),
          name: "",
          info: ""
        },
        {
          img: require("../assets/src/images/setting.png"),
          name: "",
          info: ""
        },
        {
          img: require("../assets/src/images/remind.png"),
          name: "",
          info: ""
        }
      ]
    };
  }
};
</script>
<style scoped lang="less">
@import "../styles/global";
.publicProduct {
  .product_function {
    font-size: 34px;
  }
  .show_product {
    ul > li {
      margin-bottom: 35px;
    }
    .show_product_img {
      width: 138px;
      height: 138px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      img {
        margin: 0 auto;
      }
    }
    .punch {
      margin-left: 32px;
      .punch_info {
        width: 200px;
      }
    }
  }
}
</style>


