<template>
  <div class="home pt60">
    <narBar></narBar>
    <div class="home_middle">
      <div class="dfrcb home_middle_title">
        <div class="color-fff">
          <div class="title_slogan"></div>
          <div class="fs18"></div>
          <!-- <div class="link_us text-c cp"></div> -->
        </div>
        <div>
          <img :src="img2" alt />
        </div>
      </div>
    </div>
    <div class="product_middle">
      <div class="product_info">
        <div class="product text-c"></div>
        <div>
          <div class="select cp">
            <ul class="fs20 dfrca product_name">
              <li
                ref="li"
                v-for="(item,a) in productList"
                :key="a"
                @click="handleClick(item.id)"
                class="center-tb-scatter"
              >
                <a :class="{active : current == item.id}">{{item.title}}</a>
                <div v-if="current == item.id" class="line"></div>
              </li>
            </ul>
            <el-divider class="mb40"></el-divider>
            <div :class="{hide : current !== 0}">
              <publicInfo></publicInfo>
              <div class="more_info fs20 text-c cp" @click="biaodd">
                
                <i class="el-icon-arrow-right"></i>
              </div>
            </div>
            <div :class="{hide : current !== 1}">
              <div
                class="fs20 mb40"
              ></div>
              <publicTable :tableInfo="tableInfo"></publicTable>
              <div class="more_info fs20 text-c cp" @click="dataService">
                
                <i class="el-icon-arrow-right"></i>
              </div>
            </div>
            <div :class="{hide : current !== 2}">
              <publicProduct></publicProduct>
              <div class="more_info fs20 text-c cp" @click="smallOrder">
                
                <i class="el-icon-arrow-right"></i>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="service_area">
      <div class="service_area_title color-fff text-c"></div>
      <div class="left_img">
        <img :src="img4" alt />
      </div>
      <div class="right_img">
        <img :src="img3" alt />
      </div>
      <div class="service_area_info">
        <ul class="info_list">
          <li v-for="(item,i) in areaInfo" :key="i" class="info_title cp mb20">
            <div class="info_item">
              <div class="fs20 color-fff pt30 pl30 pr30 pb30">{{item.title}}</div>
              <div class="pl30">
                <ul v-for="(a,b) in item.information" :key="b">
                  <li class="color-fff fs14 mb10">{{a.info}}</li>
                </ul>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <div class="info">
      <div class="text-c item-info">
        <div class="fs34"></div>
        <div class="info-item">
          <ul class="dfrca">
            <li>
              <span>123</span>
              <div class="fs20"></div>
            </li>
            <li>
              <span>24</span>
              <div class="fs20"></div>
            </li>
            <li>
              <span>30W</span>+
              <div class="fs20"></div>
            </li>
          </ul>
        </div>
        <!-- <div class="info-btn cp"></div> -->
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      img1: require("../assets/src/images/punch.png"),
      img2: require("../assets/src/images/bg3.png"),
      img3: require("../assets/src/images/bg1.png"),
      img4: require("../assets/src/images/bg2.png"),
      activeName: "2",
      areaInfo: [
        {
          title: "",
          information: [
            {
              info: ""
            },
            {
              info: ""
            },
            {
              info: ""
            }
          ]
        },
        {
          title: "",
          information: [
            {
              info: ""
            },
            {
              info: ""
            }
          ]
        },
        {
          title: "",
          information: [
            {
              info: ""
            },
            {
              info: ""
            },
            {
              info: ""
            },
            {
              info: ""
            }
          ]
        },
        {
          title: "App",
          information: [
            {
              info: ""
            },
            {
              info: "APP"
            },
            {
              info: ""
            }
          ]
        },
        {
          title: "",
          information: [
            {
              info: ""
            },
            {
              info: ""
            },
            {
              info: ""
            }
          ]
        },
        {
          title: "",
          information: [
            {
              info: ""
            },
            {
              info: ""
            },
            {
              info: ""
            }
          ]
        }
      ],
      productList: [
        {
          id: 0,
          title: ""
        },
        {
          id: 1,
          title: ""
        },
        {
          id: 2,
          title: ""
        }
      ],
      tableInfo: [
        {
          id: "1",
          name: "(EPC)",
          time: "20192",
          area: "",
          price: "8254.42"
        },
        {
          id: "2",
          name: "EPC ",
          time: "20192",
          area: "",
          price: "1000000.00"
        },
        {
          id: "3",
          name: "() EPC",
          time: "20192",
          area: "",
          price: "700.00"
        },
        {
          id: "4",
          name: "(EPC)",
          time: "20193",
          area: "",
          price: "12200.00"
        },
        {
          id: "5",
          name: "EPC ",
          time: "20193",
          area: "",
          price: "1550.00"
        },
        {
          id: "6",
          name: "EPC",
          time: "20193",
          area: "",
          price: "9469.60"
        }
      ],
      current: 0
    };
  },
  methods: {
    handleClick(index) {
      this.current = index;
    },
    returnTop() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    biaodd() {
      this.$router.push({ path: '/productBig', query: { id: 0 } });
      this.returnTop();
    },
    dataService() {
      this.$router.push({ path: '/dataService', query: { id: 1 } });
      this.returnTop();
    },
    smallOrder() {
      this.$router.push({ path: '/smallOrder', query: { id: 2 } });
      this.returnTop();
    }
  },
  created() {}
};
</script>
<style scoped lang="less">
@import "../styles/global";
@width: 100%;
@bg-black: #000000;
.home {
  width: 100%;
  .home_middle {
    background-color: @mainColor !important;
    height: 468px;
    .home_middle_title {
      width: @bg-width;
      height: 468px;
      margin: 0 auto;
    }
    .title_slogan {
      font-size: 38px;
      margin-bottom: 16px;
    }
    .link_us {
      width: 182px;
      height: 48px;
      line-height: 48px;
      border-radius: 24px;
      border: 1px solid #fff;
      margin-top: 42px;
    }
    img {
      width: 504px;
      height: 410px;
    }
  }
  .product_middle {
    padding: 80px 0;
    background-color: @bgColor;
    .product_info {
      width: @bg-width;
      margin: 0 auto;
      .product {
        margin-bottom: 80px;
        font-size: 34px;
        ul {
          color: #424040;
        }
      }
      .more_info {
        width: 222px;
        height: 56px;
        border-radius: 28px;
        line-height: 56px;
        border: 1px solid #dddfe4;
        color: @mainColor;
        margin: 0 auto;
      }
    }
    .active {
      font-weight: 600;
    }
    .product_name li {
      height: 60px;
      width: 160px;
    }
    .line {
      width: 100px;
      height: 6px;
      border: none;
      background-color: #000000;
    }
  }
  .service_area {
    background-color: @mainColor;
    height: 765px;
    padding-top: 80px;
    position: relative;
    .left_img {
      position: absolute;
      bottom: 0;
      left: 65px;
    }
    .right_img {
      top: 0;
      right: 0;
      position: absolute;
    }
    .service_area_title {
      margin-bottom: 80px;
      font-size: 34px;
    }
    .service_area_info {
      margin: 0 auto;
      width: @bg-width;
      min-width: @bg-width;
      .info_list {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        width: @width;
        transition: all 2s ease-in-out; //2s
        .info_title {
          height: 228px;
          width: 380px;
          border-radius: 4px;
          background-color: #4468e5;
          .info_item {
            height: 100%;
            width: 100%;
            border-radius: 4px;
          }
          .info_item:hover {
            transform: scale(1.03, 1.07);
            border: 1px solid #ffffff;
            background-color: #4468e5;
          }
        }
      }
    }
  }
  .info {
    height: 748px;
    background: #ffffff url(../assets/src/images/bg4.png) no-repeat center;
    .item-info {
      width: 1000px;
      padding-top: 80px;
      min-width: 1000px;
      margin: 0 auto;
      .info-item {
        margin: 120px 0;
        ul > li {
          span {
            font-size: 72px;
          }
        }
      }
      .info-btn {
        width: 182px;
        height: 48px;
        line-height: 48px;
        border: 1px solid #000000;
        border-radius: 24px;
        margin: 0 auto;
      }
    }
  }
}
</style>


