                    info: ""
                },
                {
                    title: "",
                    info: ""
                },
                {
                    title: "",
                    info: ""
                },
                {
                    title: "",
                    info: ""
                },
                {
                    title: "",
                    info: ""
                },
                {
                    title: "",
                    info: "1 23"
                },
            ]
        }
    }
}
</script>
<style scoped lang="less">
@import "../styles/global";
@fontColor: #424040;
.publicInfo {
    .item_left,.item_right {
        height: 180px;
        width: 580px;
    }
    .item_left {
        background: #ffffff url(../assets/src/images/bg5.png) no-repeat;
        .left_info {
            color: @fontColor;
        }
    }
    .item_right {
        background: #ffffff url(../assets/src/images/bg6.png) no-repeat;
        .right_info {
            color: @fontColor;
        }
    }
    ul {
        width: 100%;
        height: 100%;
        li {
            width: 340px;
        }
    }
}
</style>


