<template>
  <div class="productInfo bg-fff">
    <narBar @toChildren="getParams"></narBar>
    <topBar :titleInfo="titleInfo"></topBar>
    <currentTab
      :productList="productList"
      @childValue="getValue"
      class="pt30 bg-fff"
      :tabNav="tabNav"
    ></currentTab>
    <div :class="{hide : tabNav !== 0}" class="bg-fff current0">
      <div class="product_info">
        <div class="biaodd">
          <div class="fs28"></div>
        </div>
        <div
          class="fs20 mt40 mb40"
        >720w+400w+430w+500w+80%</div>
        <div class="fs34 text-c user_title"></div>
        <publicInfo class="pb40"></publicInfo>
      </div>
      <div class="biaodd_info pt80 pb40">
        <div class="biaodd_item">
          <div class="fs34 text-c mb80"></div>
          <div class="dfrcb">
            <div class="biaodd_item_img">
              <img :src="img" alt />
            </div>
            <div class="item_info">
              <div class="fs16 f600 mb20"></div>
              <div class="bottom_info mb40 fs14">
                
                <span>500w+</span>
                <span>10W+</span>
                <span>50W+</span>
                <span>200w+</span> 
                <span>400w+</span>
                <span>300w+</span>
                <span>430w+</span>
                <span>400w+</span>
                <span>320w+</span>
              </div>
              <ul v-for="(item,i) in biaoddList" :key="i">
                <li>
                  <div class="fs16 f600 mb20">{{item.title}}</div>
                  <ul class="mb40">
                    <li v-for="(a,b) in item.itemList" :key="b" class="bottom_info fs14">{{a.info}}</li>
                  </ul>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <div class="pt80 pb80 service">
        <div class="core_service">
          <div class="text-c fs34 mb40"></div>
          <div class="item">
            <ul class="dfrcb">
              <div class="img1">
                <img :src="img1" alt />
              </div>
              <div class="img2">
                <img :src="img2" alt />
              </div>
              <div class="img3">
                <img :src="img3" alt />
              </div>
              <li v-for="(item,index) in serviceList" :key="index" class="mt20">
                <div :class="{'hideBox': index == 8}" class="li_item">
                  <div class="fs16 f600 pt20 pb10 pl20 pr20">{{item.title}}</div>
                  <div class="fs14 f400 pl20 pr20">{{item.content}}</div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="CQ_code pt80 pb80">
        <div class="link_code">
          <div class="fs34 text-c mb80"></div>
          <div class="fs20 mb40">App</div>
          <div class="dfrca">
            <div>
              <div class="code_item">
                <img :src="img4" alt />
              </div>
              <div class="text-c fs20 mt30">App Store/</div>
            </div>
            <div>
              <div class="code_item">
                <img :src="img4" alt />
              </div>
              <div class="text-c fs20 mt30"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div :class="{hide : tabNav !== 1}" class="current1">
      <div class="data">
        <div class="data_service">
          <div class="fs28"></div>
        </div>
        <div
          class="fs20 mt40 mb40"
        ></div>
        <div>
          <div class="fs34 text-c mt40 mb40"></div>
          <div class="fs20 mb20">1.20197131EPC25</div>
        </div>
      </div>
    </div>
    <div :class="{hide : tabNav !== 2}"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      img: require("../assets/src/images/biaodd_bg3.png"),
      img1: require("../assets/src/images/biaodd_icon1.png"),
      img2: require("../assets/src/images/biaodd_icon2.png"),
      img3: require("../assets/src/images/biaodd_icon3.png"),
      img4: require("../assets/src/images/biaodd_code1.png"),
      img5: require("../assets/src/images/biaodd_code2.png"),
      productList: [
        {
          id: 0,
          title: "",
          path: "/productBig"
        },
        {
          id: 1,
          title: "",
          path: "/productBig"
        },
        {
          id: 2,
          title: "",
          path: "/productBig"
        }
      ],
      tabNav: "",
      biaoddList: [
        {
          title: "",
          itemList: [
            {
              info: ""
            },
            {
              info:
                ""
            }
          ]
        },
        {
          title: "",
          itemList: [
            {
              info: ""
            },
            {
              info: ""
            },
            {
              info:
                ""
            },
            {
              info:
                ""
            },
            {
              info:
                ""
            },
            {
              info:
                ""
            },
            {
              info: ""
            }
          ]
        }
      ],
      serviceList: [
        {
          title: "",
          content:
            ""
        },
        {
          title: "",
          content:
            ""
        },
        {
          title: "",
          content:
            "300+"
        },
        {
          title: "",
          content:
            ""
        },
        {
          title: "",
          content:
            "12"
        },
        {
          title: "",
          content:
            ""
        },
        {
          title: "",
          content:
            ""
        },
        {
          title: "",
          content:
            ""
        },
        {
          title: "",
          content: ""
        }
      ],
      titleInfo: {
        themem: "",
        title: "Product Presentation"
        // companyInfo:
        //   "2017 2017"
      }
    };
  },
  methods: {
    getValue(data) {
      this.tabNav = data;
    }
  },
  created() {
    this.tabNav = this.$route.query.id;
  }
};
</script>
<style lang="less">
@import "../styles/global";
@bg-width: 1180px;
.productInfo {
  .current0 {
    .product_info {
      width: @bg-width;
      min-width: @bg-width;
      margin: 0 auto;
      .biaodd {
        height: 120px;
        line-height: 120px;
        border-bottom: 1px solid #dddfe4;
      }
      .user_title {
        margin-bottom: 80px;
      }
    }
    .biaodd_info {
      background-color: @bgColor;
      .biaodd_item {
        width: @bg-width;
        min-width: @bg-width;
        margin: 0 auto;
        .biaodd_item_img {
          width: 577px;
          height: 453px;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
      .item_info {
        .bottom_info {
          width: 485px;
          span {
            color: @mainColor;
          }
        }
      }
    }
    .service {
      .core_service {
        width: @bg-width;
        min-width: @bg-width;
        margin: 0 auto;
        img {
          width: 100%;
          height: 100%;
        }
        .item {
          position: relative;
          ul > li {
            .img1 {
              width: 89px;
              height: 109px;
              position: absolute;
              top: 61px;
              right: 457px;
            }
            .img2 {
              width: 87px;
              height: 93px;
              position: absolute;
              bottom: 33px;
              left: 264px;
            }
            .img3 {
              width: 56px;
              height: 51px;
              position: absolute;
              right: 295px;
              bottom: 56px;
            }
            .li_item {
              width: 380px;
              height: 134px;
              border: 1px solid rgba(221, 223, 228, 1);
            }
            .hideBox {
              border: none;
            }
          }
        }
      }
    }
    .CQ_code {
      background: @bgColor;
      .link_code {
        width: @bg-width;
        min-width: @bg-width;
        margin: 0 auto;
        .code_item {
          width: 342px;
          height: 342px;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
    //
    .publicInfo /deep/ .item_left {
      background: url(../assets/src/images/biaodd_bg1.png) no-repeat !important;
    }
    .publicInfo /deep/ .item_right {
      background: url(../assets/src/images/biaodd_bg2.png) no-repeat !important;
    }
  }
  .current1 {
    .data {
      width: @bg-width;
      min-width: @bg-width;
      margin: 0 auto;
      .data_service {
        height: 120px;
        line-height: 120px;
        border-bottom: 1px solid #dddfe4;
      }
    }
  }
}
</style>


